<template>
  <div id="centerLeft2">
    <div class="down">
      <div class="ranking bg-color-black">
        <span>
          <icon name="chart-line" class="text-icon"></icon>
        </span>
        <span class="fs-xl text mx-2 mb-1 pl-3" style="font-size: 15px">返校信息</span>
        <dv-scroll-ranking-board class="dv-scr-rank-board mt-1" :config="config"/>
      </div>
    </div>
  </div>
</template>

<script>
import {Return} from '@/axios/api'
let date
export default {
  name: "centerLeft2",
  created() {
    this.getData()
  },
  methods: {
    async getData() {
      date = await Return().then(res => res.data)
      this.config = {
        data: date,
        carousel: 'single',
        unit: '人'
      }
    }
  },
  data() {
    return {
      config: {
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.down {
  padding: 6px 4px;
  padding-bottom: 0;
  width: 400px;
  height: 400px;
  display: flex;
  justify-content: space-between;

  .bg-color-black {
    border-radius: 5px;
  }

  .ranking {
    PADDING: 10PX;
    WIDTH: 59%;

    .DV-SCR-RANK-BOARD {
      HEIGHT: 350PX;
    }
  }
}
</style>
