<template>
  <div>
    <Chart :cdata="cdata"/>
  </div>
</template>

<script>
import Chart from './chart.vue'
import {getYnData} from "@/axios/api";

export default {
  data() {
    return {
      cdata: {
        // X轴坐标
        category: [],
        // 新增本土
        lineData: [],
        // 新增境外
        barData: []
      }
    };
  },
  components: {
    Chart,
  },
  mounted() {
    this.getYnData()
  },
  methods: {
    async getYnData() {
      let data = await getYnData().then(res => res.data)
      if (data > 0) {
        this.cdata.category = data[2]
        this.cdata.lineData = data[0]
        this.cdata.barData = data[1]
      } else {
        this.cdata.category = ["2022-06-08", "2022-06-09", "2022-06-10", "2022-06-11", "2022-06-12"]
        this.cdata.barData = [0, 0, 0, 0, 0,]
        this.cdata.lineData = [0, 0, 0, 0, 4]
      }
    }
  }
};
</script>

