<template>
  <div>
    <Chart :cdata="cdata" />
  </div>
</template>

<script>
import Chart from './chart.vue';
import {getYunnanMap} from '@/axios/api'
let metadata = null;
export default {
  data () {
    return {
      cdata: [
      ]
    }
  },
  components: {
    Chart,
  },
  mounted () {
    this.setup();
  },
  methods: {
    // 获取后端数据
    async setup(){
      metadata = await getYunnanMap().then(res => res.data)
      this.cdata = metadata
    }
  }
}
</script>

