<template>
  <div style="text-align: center;">
    <Echart
        id="bottomCenterChart"
        ref="bottomCenterChartRef"
        :options="options"
        height="700px"
        width="770px"
    ></Echart>
  </div>
</template>

<script>
import Echart from '@/common/echart';

export default {
  data() {
    return {
      options: {},
    };
  },
  components: {
    Echart,
  },
  props: {
    cdata: {
      type: Array,
      default: () => [],
    },
  },
  watch: {
    cdata: {
      handler(newData) {
        // 设置点的位置(经纬度)
        const geoCoordMap = {
          昆明市: [102.839667, 24.885953 ,20],
          昭通市: [103.723311, 27.344057,20],
          大理白族自治州: [100.274223, 25.612206,20],
          迪庆藏族自治州: [99.70948, 27.825264,20],
          德宏傣族景颇族自治州: [98.591419, 24.438031,20],
          西双版纳傣族自治州: [100.803836, 22.013792,20],
          曲靖市: [103.802685, 25.496328,20],
          红河哈尼族彝族自治州: [103.38215, 23.369914,20],
          文山壮族苗族自治州: [104.221606, 23.404187,20],
          玉溪市: [102.5537, 24.357512,20],
          普洱市: [100.98114, 22.788486,20],
          保山市: [99.168373, 25.117882,20],
          临沧市: [100.10566, 23.914336,20],
          丽江市: [100.23357, 26.862521,20],
          楚雄彝族自治州: [101.534082, 25.051226,20],
          怒江傈僳族自治州: [98.863189, 25.823736,20]
        };
        let seriesData = [
          {
            name: '昆明市',
          },
          {
            name: '昭通市',
          },
          {
            name: '大理白族自治州',
          },
          {
            name: '迪庆藏族自治州',
          },
          {
            name: '德宏傣族景颇族自治州',
          },
          {
            name: '西双版纳傣族自治州',
          },
          {
            name: '曲靖市',
          },
          {
            name: '红河哈尼族彝族自治州',
          },
          {
            name: '文山壮族苗族自治州',
          },
          {
            name: '玉溪市',
          },
          {
            name: '普洱市',
          },
          {
            name: '保山市',
          },
          {
            name: '临沧市',
          },
          {
            name: '丽江市',
          },
          {
            name: '楚雄彝族自治州',
          },
          {
            name: '怒江傈僳族自治州',
          },
        ];
        let convertData = function (data) {
          let scatterData = [];
          for (var i = 0; i < data.length; i++) {
            var geoCoord = geoCoordMap[data[i].name];
            if (geoCoord) {
              scatterData.push({
                name: data[i].name,
                value: geoCoord.concat(data[i].value),
              });
            }
          }
          return scatterData;
        };
        this.options = {
          showLegendSymbol: true,
          title: {
            text: '返校学生分布图',
            textStyle: {
              color: 'white',
              fontSize: '25'

            },
            left: 'center',
            top: '40px'

          },
          tooltip: {
            trigger: 'item',
            textStyle: {
              fontSize: 14,
              lineHeight: 22,
            },
            position: point => {
              // 固定在顶部
              return [point[0] + 50, point[1] - 20];
            },
          },
          visualMap: { // 左侧小导航图标
            show: true,
            x: 'left',
            y: 'bottom',
            textStyle: {
              fontSize: 10,
              color: '#fff'
            },
            splitList: [{
              start:0,
              end: 200
            },
              {
                start: 200,
                end: 400
              },
              {
                start: 400,
                end: 600
              },
              {
                start: 600,
                end: 800
              },
              {
                start: 800
              }
            ],
            color: ['#E55B25', '#FF9900', '#99CCFF','#3399CC', '#003366']
          },
          // 底部背景
          geo: {
            show: true,
            aspectScale: 0.85, //长宽比
            zoom: 1.2,
            top: '10%',
            left: '16%',
            map: '云南',
            roam: false,
            itemStyle: {
              normal: {
                areaColor: 'rgba(0,0,0,0)',
                shadowColor: 'rgba(7,114,204, .8)',
                shadowOffsetX: 5,
                shadowOffsetY: 5,
              },
              emphasis: {
                areaColor: '#00aeef',
              },
            },
          },
          series: [
            {
              name: '学生人数',
              type: 'map',
              aspectScale: 0.85, //长宽比
              zoom: 1.2,
              mapType: '云南', // 自定义扩展图表类型
              top: '10%',
              left: '16%',
              itemStyle: {
                normal: {
                  color: 'red',
                  areaColor: 'rgba(19,54,162, .5)',
                  borderColor: 'rgba(0,242,252,.3)',
                  borderWidth: 1,
                  shadowBlur: 7,
                  shadowColor: '#00f2fc',
                },
                emphasis: {
                  areaColor: '#CC3333',
                  borderColor: 'rgba(0,242,252,.6)',
                  borderWidth: 2,
                  shadowBlur: 10,
                  shadowColor: '#00f2fc',
                },
              },
              label: {
                formatter: params => `${params.name}`,
                show: true,
                position: 'insideRight',
                textStyle: {
                  fontSize: 14,
                  color: '#efefef',
                },
                emphasis: {
                  textStyle: {
                    color: '#fff',
                  },
                },
              },
              data: newData,
            },
            {
              type: 'effectScatter',
              coordinateSystem: 'geo',
              symbolSize: 7,
              effectType: 'ripple',
              legendHoverLink: false,
              showEffectOn: 'render',
              rippleEffect: {
                period: 4,
                scale: 2.5,
                brushType: 'stroke',
              },
              zlevel: 1,
              itemStyle: {
                normal: {
                  color: '#99FBFE',
                  shadowBlur: 5,
                  shadowColor: '#fff',
                },
              },
              data: convertData(seriesData),
            },
          ],
        };
        // 重新选择区域
        this.handleMapRandomSelect();
      },
      immediate: true,
      deep: true,
    },
  },
  methods: {
    // 开启定时器
    startInterval() {
      const _self = this;
      // 应通过接口获取配置时间，暂时写死5s
      const time = 2000;
      if (this.intervalId !== null) {
        clearInterval(this.intervalId);
      }
      this.intervalId = setInterval(() => {
        _self.reSelectMapRandomArea();
      }, time);
    },
    // 重新随机选中地图区域
    reSelectMapRandomArea() {
      const length = 9;
      this.$nextTick(() => {
        try {
          const map = this.$refs.bottomCenterChartRef.chart;
          let index = Math.floor(Math.random() * length);
          while (index === this.preSelectMapIndex || index >= length) {
            index = Math.floor(Math.random() * length);
          }
          map.dispatchAction({
            type: 'mapUnSelect',
            seriesIndex: 0,
            dataIndex: this.preSelectMapIndex,
          });
          map.dispatchAction({
            type: 'showTip',
            seriesIndex: 0,
            dataIndex: index,
          });
          map.dispatchAction({
            type: 'mapSelect',
            seriesIndex: 0,
            dataIndex: index,
          });
          this.preSelectMapIndex = index;
        } catch (error) {
          console.log(error)
        }
      });
    },
    handleMapRandomSelect() {
      this.$nextTick(() => {
        try {
          const map = this.$refs.bottomCenterChartRef.chart;
          const _self = this;
          setTimeout(() => {
            _self.reSelectMapRandomArea();
          }, 0);
          // 移入区域，清除定时器、取消之前选中并选中当前
          map.on('mouseover', function (params) {
            clearInterval(_self.intervalId);
            map.dispatchAction({
              type: 'mapUnSelect',
              seriesIndex: 0,
              dataIndex: _self.preSelectMapIndex,
            });
            map.dispatchAction({
              type: 'mapSelect',
              seriesIndex: 0,
              dataIndex: params.dataIndex,
            });
            _self.preSelectMapIndex = params.dataIndex;
          });
          // 移出区域重新随机选中地图区域，并开启定时器
          map.on('globalout', function () {
            _self.reSelectMapRandomArea();
            _self.startInterval();
          });
          this.startInterval();
        } catch (error) {
          console.log(error)
        }
      });
    },
  },
};
</script>
